let axios = require('axios');
let crypto = require('crypto');

/**
 *
 *  nodejs 
 * API: https://help.aliyun.com/document_detail/44364.html?spm=5176.8195934.507901.11.pLzahV
 * github: https://github.com/freecto
 *
 */
var AliyunSmsUtil = {

    config: {   // (: ianwang003 4.5)
        AccessKeyId: "LTAI5efg83spxQYO",  //ID
        AccessKeySecret: "lqytu24ufPcOMqnXwWqM2JcqdiGvXZ", //
        TemplateCode: "SMS_75990311", //CODE
        SignName: "", //
        SignatureMethod: "HMAC-SHA1", //HMAC-SHA1
        Version: "2017-05-25", //APIYYYY-MM-DD2016-09-27
        Action: "SendSms", //SingleSendSms
        Format: "JSON", //JSONXMLXML
        SignatureVersion: "1.0" //1.0
    },


//         Format=XML&SignName=%E9%98%BF%E9%87%8C%E4%BA%91%E7%9F%AD%E4%BF%A1%E6%B5%8B%E8%AF%95%E4%B8%93%E7%94%A8&
// SignatureMethod=HMAC-SHA1
// TemplateCode=SMS_71390007
// Signature=%2BexbambPmgIG1MAYwP%2B7GPwz4RM%3D&
// Timestamp=2017-06-26T10%3A20%3A09Z
// TemplateParam=%7B%22customer%22%3A%22junmu%22%7D&
// OutId=yourOutId
// AccessKeyId=LTAIbGaUjbqB7CdA&
// Action=SendSms&
// RegionId=cn-hangzhou&
// SignatureNonce=81fc25c0-5cf9-4c2c-8fc8-994d8ed772b0&
// PhoneNumbers=13291835394&Version=2017-05-25&
// SignatureVersion=1.0 HTTP/1.1

    /**
     * 
     * @param data  {code:"1234", phone:"13062706593"}
     * @param callback 
     */
    sendMessage: function (data, callback) {
        let that = this;
        let param = {
            'Action': that.config.Action, //SingleSendSms
            'SignName': that.config.SignName, //
            'TemplateCode': data.TemplateCode, //CODE
            'Version': that.config.Version, //APIYYYY-MM-DD2016-09-27
            'Format': that.config.Format, //JSONXMLXML
            'AccessKeyId': that.config.AccessKeyId, //ID
            'SignatureMethod': that.config.SignatureMethod,  //HMAC-SHA1
            'SignatureVersion': that.config.SignatureVersion, //1.0
            'SignatureNonce': ""+Math.random(), //
            'Timestamp': new Date().toISOString(), //ISO8601UTCYYYY-MM-DDThh:mm:ssZ
            'PhoneNumbers': data.phone, //
            'TemplateParam':data.TemplateParam  //
          //  'TemplateParam': ,//
        };
        param.Signature = this.signParameters(param, that.config.AccessKeySecret);

        // let api_url = 'https://sms.aliyuncs.com/';
        let api_url = 'https://dysmsapi.aliyuncs.com/';

        axios({
            method:'post',
            url: api_url,
            params:param,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
            }
        }).then(function(err,response,data){
            console.log("sendmessage function:",err, data);
            if (callback) {
                //      console.log("data:",data)
                //       console.log("err:",err)
                        callback(err, response, data);
                console.log("sendmessage callback function:",err, data);
                    }



        }) .catch(function (response) {
            console.log("error response")
            console.log(response);
        })
    },

   /**
     *  
     * @param param 
     * @param AccessKeySecret 
     */
    signParameters: function (param, AccessKeySecret) {
        let param2 = {}, data=[];
        let oa = Object.keys(param).sort();
        for (let i = 0; i < oa.length; i++) {
            let key = oa[i];
            param2[key] = param[key];
        }
        for (let key in param2) {
            data.push(encodeURIComponent(key) + '=' + encodeURIComponent(param2[key]));
        }
        data = data.join('&');
        let StringToSign = 'POST' + '&' + encodeURIComponent('/') + '&' + encodeURIComponent(data);
        AccessKeySecret = AccessKeySecret + '&';
        return crypto.createHmac('sha1', AccessKeySecret).update(new Buffer(StringToSign, 'utf-8')).digest('base64');
    },

    /**
     * 
     */
    // send:function(data,function(err, response){
    //   //  var data={code:"1234", phone:"18936890561"};
    //     this.sendMessage(data, function(err, response, data){
    //         console.log("send function:",err, response,data);
    //        // console.log("sended")
    //     })
    // }),

    send:function(data){
      //  var data={code:"1234", phone:"18936890561"};
        this.sendMessage(data, function(err, response, data){
            console.log("send function:",err, response,data);

           // console.log("sended")
        })
    }

};

module.exports = AliyunSmsUtil;

